.header {
    position: relative;
    background: url('../img/header-bg-3.jpg') center no-repeat;
    background-size: cover;
    &--lk {
        height: 100vh;
    }
}

.header__wrapper {
    padding: 0;
}

.header__top {
    width: 100%;
    padding: 15px 0;
}

.logo {
    margin-right: 25px;
}

.navigation {
    display: flex;
    align-items: center;
    margin-right: auto;
}

.flag__item {
    display: inline-block;
    margin-right: 10px;
    &:last-child {
        // margin-right: 80px;
        margin-right: 30px;
    }
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-transform: uppercase;
    font-size: 32px;
    padding-top: 45px;
    @include phones {
        font-size: 17px;
    }
}

.logo__big {
    margin-right: 10px;
    @include phones {
        width: 70px;
        height: 70px;
    }
}

.moto {
    color: #fff;
    font-family: 'Comp Pro';
    font-weight: 300;
    font-size: 30px;
    @include phones {
        font-size: 20px;
    }
}

.main__content {
    padding: 100px 0;
    padding-bottom: 60px;
}

.text {
    font-weight: 700;
    color: #fff;
    font-size: 42px;
    line-height: 24px;
}

.offer {
    color: #71dde0;
    text-transform: uppercase;
    font-family: 'Comp Pro';
    font-size: 22px;
}

.social__text {
    color: #fff;
    font-size: 22px;
    margin-bottom: 10px;
}

.social__link {
    display: inline-block;
    margin-right: 12px;
    transition: .3s ease;
    &:hover {
        filter: brightness(1.5);
    }
    &--lk {
        margin-right: 85px;

        @include phones {
            margin-right: 30px;
        }
    }
}

.social__pic {
    @include phones {
        width: 34px;
        height: 34px;
    }
    &--lk {
        width: 40px;
        height: 40px;
    }
}

.whitepaper {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 85px 0;
    @include phones {
        flex-direction: column;
    }
}

.whitepaper__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: uppercase;
    line-height: 1;
    color: #bffffe;
    &:hover {
        color: #11c7ef;
        text-decoration: none;
    }
}

.whitepaper__pic {
    max-width: 100%;
    height: auto;
    margin: 0 20px;
    @include phones {
        display: none;
    }
}

.whitepaper__text {
    @include phones {
        margin-bottom: 10px;
        &:last-child {
            margin: 0;
        }
    }
}

.download {
    font-weight: 500;
    font-size: 30px;
}

.wp {
    font-family: 'Comp Pro';
    font-weight: 300;
    font-size: 38px;
}

.plate {
    position: absolute;
    top: 16%;
    right: 0;
    display: flex;
    align-items: center;
    @include tablets {
        display: none;
    }
}

.plate__text {
    color: #71dde0;
    font-family: 'Comp Pro';
    font-weight: 300;
    font-size: 36px;
    line-height: 1;
}