.team {
    background: url('../img/dots-left-bg.png') left no-repeat, url('../img/dots-right-bg.png') right no-repeat;
    @include tablets {
        background: url('../img/dots-left-bg.png') -65% no-repeat, url('../img/dots-right-bg.png') 200% no-repeat;
    }
}

.team__wrapper {
    padding-top: 0;
}

.team__title {
    text-align: center;
    color: #006173;
    font-size: 36px;
    font-weight: 600;
    margin-bottom: 40px;
}

.team__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include tablets {
        display: block;
    }
}

.team__member {
    border: 1px solid #cdcdcd;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 20px 20px;
    margin-bottom: 20px;
    flex-basis: 49%;
    @include tablets {
        flex-basis: 100%; // height: 100%;
        width: 500px; // height: 300px;
        border: none;
    }
}

.member__left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 30px;
}

.member__photo {
    margin-bottom: 10px;
}

.member__pic {
    @include phones {
        width: 100px;
        height: 100px;
    }
}

.member__name {
    color: #006173;
    font-size: 32px;
    font-weight: 600;
    line-height: 1;
}

.member__pos {
    color: #11c7ef;
    font-size: 1rem;
    font-weight: 600;
}

.member__desc {
    line-height: 1.2;
}