.technical-features {
    background: url('../img/dots-left-bg.png') left no-repeat, url('../img/dots-right-bg.png') right no-repeat, url('../img/token-bg.jpg') center no-repeat /cover;
}

.technical__title {
    text-align: center;
    color: #006173;
    font-size: 36px;
    font-weight: 600;
}

.technical__subtitle {
    text-align: center;
    font-size: 1rem;
    color: #777777;
    margin-bottom: 40px;
}

.technical__item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;
    @include phones {
        flex-direction: column;
        align-items: center;
    }
}

.technical__image {
    margin-right: 20px;
}

.technical__desc-title {
    color: #006173;
    font-size: 24px;
    font-weight: 600;
    @include phones {
        text-align: center;
    }
}

.technical__desc-text {
    font-size: 15px;
    @include phones {
        text-align: justify;
    }
}