.cost-structure {
    background: url('../img/dots-left-bg.png') left no-repeat, url('../img/dots-right-bg.png') right no-repeat, url('../img/token-bg.jpg') center no-repeat /cover;
    @include tablets {
        background: url('../img/dots-left-bg.png') -65% no-repeat, url('../img/dots-right-bg.png') 200% no-repeat, url('../img/token-bg.jpg') center no-repeat /cover;
    }
}

.cost__wrapper {
    padding: 75px 0;
}

.cost__title {
    text-align: center;
    color: #046375;
    font-weight: 500;
    font-size: 36px;
    margin-bottom: 90px;
    @include phones {
        margin-bottom: 50px;
    }
}

.chart__wrapper {
    position: relative;
    width: 100%;
    height: 762px;
    text-align: center;
    @include tablets {
        position: initial;
        height: auto;
    }
}

.chart__pic {
    max-width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    @include tablets {
        position: initial;
        top: initial;
        left: initial;
        transform: none;
    }
}

.chart__desktop {
    @include tablets {
        display: none;
    }
}

.chart__mobile {
    display: none;
    @include tablets {
        display: initial;
    }
}