.ico-structure {
    background: url('../img/dots-left-bg.png') left no-repeat, url('../img/dots-right-bg.png') right no-repeat, url('../img/ico-bg.jpg') center no-repeat /cover;
    color: #fff;
    @include tablets {
        background: url('../img/ico-bg.jpg') center no-repeat /cover;
    }
}

.ico__wrapper {
    padding: 75px 0;
}

.ico__title {
    text-align: center;
    color: #fff;
    font-weight: 500;
    font-size: 36px;
    margin-bottom: 90px;
}

.chart__text {
    margin-top: 70px;
}

.chart__image {
    max-width: 100%;
    height: auto;
}

