.footer {
    position: relative;
    background: url('../img/footer-bg1.jpg') center no-repeat;
    background-size: cover;
    overflow: hidden;
}

.footer__wrapper {
    padding: 75px 0;
    padding-bottom: 40px;
}

.contacts__title {
    font-weight: 500;
    color: #fff;
    font-size: 36px;
    text-align: center;
}

.contacts__company {
    text-align: center;
    color: #fff;
    font-weight: 500;
    font-size: 24px;
    margin: 25px 0;
}

.contacts__adress {
    font-weight: 500;
    font-size: 24px;
    color: #fff;
    text-align: center;
    margin-bottom: 55px;
}

.contacts__email {
    color: #fff;
}

.map {
    margin-bottom: 45px;
    width: 100%;
    height: 340px;
}

.form {
    margin-bottom: 25px;
}

.form__elem {
    width: 100%;
}

.form__row {
    display: flex;
    justify-content: center;
}

.email {
    width: 100%;
    border: none;
    border-radius: 3px;
    padding: 10px 20px;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 16px;
}

.submit {
    background-color: transparent;
    border: 1px solid #fff;
    border-left: none;
    border-radius: 3px;
    padding: 10px 30px;
    cursor: pointer;
}

// .contacts__email-group {
//     margin-top: 35px;
//     margin-bottom: 20px;
// }
.footer__logo {
    margin-top: 35px;
    margin-bottom: 20px;
}

.footer__text {
    color: #fff;
    line-height: 1.2;
}

.footer__navigation {
    display: flex;
    justify-content: flex-end;
    @include phones {
        justify-content: flex-start;
        margin: 5px 0;
    }
}

.footer__telegram {
    // text-align: right;
    color: #fff;
}

.content {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: left;
    @include phones {
        justify-content: flex-start;
    }
}

.email__link {
    color: #71dde0;
    &:hover {
        color: #71dde0;
    }
}

.telegram__link {
    color: #71dde0;
    &:hover {
        color: #71dde0;
    }
}

.link-icon {
    // margin-right: 10px;
    margin-left: 10px;
}
